// components/FoodRow.tsx
"use client";

import { useEffect } from "react";
import { loadLocalDB, saveLocalDB } from "@/lib/localStore";

type SelectedFood = {
  name: string;
  term: string;
  quantity?: number;
};

type LocalDB = {
  foods_selected: SelectedFood[];
};

type FoodRowProps = {
  food: SelectedFood;
};

export default function FoodRow({ food }: FoodRowProps) {
  useEffect(() => {
    if (!food?.term) return;

    (async () => {
      const db = (await loadLocalDB<LocalDB>()) ?? { foods_selected: [] };

      const idx = db.foods_selected.findIndex(f => f.term === food.term);

      if (idx === -1) {
        db.foods_selected.push(food);
      } else {
        db.foods_selected[idx] = food;
      }

      await saveLocalDB(db);
    })();
  }, [food.term, food.quantity, food.name]);

  return (
    <div className="flex justify-between text-sm">
      <span>{food.term}</span>
      <span className="text-muted-foreground">
        {food.quantity ?? ""}
      </span>
    </div>
  );
}
